// src/hooks/useTransactions.ts
import { useState } from 'react';
import { Transaction } from '@/types';
import { supabase } from '@/lib/supabase';

export const useTransactions = () => {
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState<string | null>(null);

  // –ü–æ–ª—É—á–∏—Ç—å —Ç—Ä–∞–Ω–∑–∞–∫—Ü–∏–∏ —É—á–µ–Ω–∏–∫–∞
  const fetchTransactions = async (studentId: string): Promise<Transaction[] | null> => {
    setLoading(true);
    setError(null);
    try {
      const { data, error } = await supabase
        .from('Transactions')
        .select('*')
        .eq('student_id', studentId)
        .order('created_at', { ascending: false });

      if (error) throw error;
      return (data || []).map(t => ({
        ...t,
        id: t.id,
        studentId: t.student_id,
        amount: t.amount,
        type: t.amount >= 0 ? 'add' : 'subtract',
        description: t.description,
        timestamp: t.created_at ? new Date(t.created_at) : new Date(),
      }));
    } catch (err: any) {
      setError(err.message);
      console.error('–û—à–∏–±–∫–∞ –ø—Ä–∏ –∑–∞–≥—Ä—É–∑–∫–µ —Ç—Ä–∞–Ω–∑–∞–∫—Ü–∏–π:', err);
      return null;
    } finally {
      setLoading(false);
    }
  };

  // –°–æ–∑–¥–∞—Ç—å —Ç—Ä–∞–Ω–∑–∞–∫—Ü–∏—é
  const createTransaction = async (
    studentId: string,
    amount: number,
    description: string
  ): Promise<Transaction | null> => {
    setLoading(true);
    setError(null);
    try {
      const { data, error } = await supabase
        .from('Transactions')
        .insert({
          student_id: studentId,
          amount,
          description,
        })
        .select()
        .single();

      if (error) throw error;
      return {
        ...data,
        id: data.id,
        studentId: data.student_id,
        amount: data.amount,
        type: data.amount >= 0 ? 'add' : 'subtract',
        description: data.description,
        timestamp: data.created_at ? new Date(data.created_at) : new Date(),
      };
    } catch (err: any) {
      setError(err.message);
      console.error('–û—à–∏–±–∫–∞ –ø—Ä–∏ —Å–æ–∑–¥–∞–Ω–∏–∏ —Ç—Ä–∞–Ω–∑–∞–∫—Ü–∏–∏:', err);
      return null;
    } finally {
      setLoading(false);
    }
  };

  // –ú–∞—Å—Å–æ–≤–æ–µ –Ω–∞—á–∏—Å–ª–µ–Ω–∏–µ (–æ–ø—Ü–∏–æ–Ω–∞–ª—å–Ω–æ ‚Äî –º–æ–∂–Ω–æ —Å–¥–µ–ª–∞—Ç—å –æ—Ç–¥–µ–ª—å–Ω–æ)
  const bulkCreateTransactions = async (
    transactionsData: { student_id: string; amount: number; description: string }[]
  ): Promise<boolean> => {
    setLoading(true);
    setError(null);
    try {
      const { error } = await supabase
        .from('Transactions')
        .insert(transactionsData);

      if (error) throw error;
      return true;
    } catch (err: any) {
      setError(err.message);
      console.error('–û—à–∏–±–∫–∞ –ø—Ä–∏ –º–∞—Å—Å–æ–≤–æ–º —Å–æ–∑–¥–∞–Ω–∏–∏ —Ç—Ä–∞–Ω–∑–∞–∫—Ü–∏–π:', err);
      return false;
    } finally {
      setLoading(false);
    }
  };

  return {
    loading,
    error,
    fetchTransactions,
    createTransaction,
    bulkCreateTransactions,
  };
};