import { useState } from "react";
import { Student } from "@/types";
import { supabase } from "@/lib/supabase";

export const useStudents = () => {
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  // –ü–æ–ª—É—á–∏—Ç—å —Å—Ç—É–¥–µ–Ω—Ç–æ–≤ (—Ç–æ–ª—å–∫–æ –Ω–µ —É–¥–∞–ª—ë–Ω–Ω—ã—Ö)
  const fetchStudents = async (groupId?: string): Promise<Student[] | null> => {
    setLoading(true);
    setError(null);

    try {
      let query = supabase
        .from('Students')
        .select('*')
        .eq('is_deleted', false); // ‚Üê —Ç–æ–ª—å–∫–æ –∞–∫—Ç–∏–≤–Ω—ã–µ

      if (groupId) {
        query = query.eq('group_id', groupId);
      }

      const { data, error } = await query.order('created_at', { ascending: false });

      if (error) throw error;

      return (data || []).map(s => ({
        id: s.id,
        name: s.name,
        groupId: s.group_id,
        birthDate: s.birth_date,
        balance: s.balance || 0,
        createdAt: s.created_at ? new Date(s.created_at) : new Date(),
        isDeleted: s.is_deleted, // ‚Üê –¥–æ–±–∞–≤–∏–ª–∏
      }));

    } catch (err: any) {
      setError(err.message);
      console.error('–û—à–∏–±–∫–∞ –ø—Ä–∏ –∑–∞–≥—Ä—É–∑–∫–µ —É—á–µ–Ω–∏–∫–æ–≤:', err);
      return null;
    } finally {
      setLoading(false);
    }
  };

  // –°–æ–∑–¥–∞—Ç—å —Å—Ç—É–¥–µ–Ω—Ç–∞
  const createStudent = async (
    name: string,
    birthDate: string,
    groupId: string,
  ): Promise<Student | null> => {
    setLoading(true);
    setError(null);
    try {
      const { data, error } = await supabase
        .from('Students')
        .insert({
          name,
          birth_date: birthDate,
          group_id: groupId,
          balance: 0,
          is_deleted: false, // ‚Üê —è–≤–Ω–æ —É–∫–∞–∑—ã–≤–∞–µ–º
        })
        .select()
        .single();

      if (error) throw error;
      return {
        id: data.id,
        name: data.name,
        groupId: data.group_id,
        birthDate: data.birth_date,
        balance: data.balance || 0,
        isDeleted: data.is_deleted,
      };
    } catch (err: any) {
      setError(err.message);
      console.error('–û—à–∏–±–∫–∞ –ø—Ä–∏ —Å–æ–∑–¥–∞–Ω–∏–∏ —É—á–µ–Ω–∏–∫–∞:', err);
      return null;
    } finally {
      setLoading(false);
    }
  };

  // –û–±–Ω–æ–≤–∏—Ç—å —Å—Ç—É–¥–µ–Ω—Ç–∞
  const updateStudent = async (
    id: string,
    name: string,
    birthDate: string,
  ): Promise<boolean> => {
    setLoading(true);
    setError(null);
    try {
      const { error } = await supabase
        .from('Students')
        .update({
          name,
          birth_date: birthDate,
        })
        .eq('id', id)
        .eq('is_deleted', false); // ‚Üê –Ω–µ–ª—å–∑—è –æ–±–Ω–æ–≤–ª—è—Ç—å —É–¥–∞–ª—ë–Ω–Ω–æ–≥–æ

      if (error) throw error;
      return true;
    } catch (err: any) {
      setError(err.message);
      console.error('–û—à–∏–±–∫–∞ –ø—Ä–∏ –æ–±–Ω–æ–≤–ª–µ–Ω–∏–∏ —É—á–µ–Ω–∏–∫–∞:', err);
      return false;
    } finally {
      setLoading(false);
    }
  };

  // "–£–¥–∞–ª–∏—Ç—å" —Å—Ç—É–¥–µ–Ω—Ç–∞ ‚Äî –ø–æ–º–µ—Ç–∏—Ç—å –∫–∞–∫ —É–¥–∞–ª—ë–Ω–Ω–æ–≥–æ
  const deleteStudent = async (id: string): Promise<boolean> => {
    setLoading(true);
    setError(null);
    try {
      const { error } = await supabase
        .from('Students')
        .update({ is_deleted: true })
        .eq('id', id);

      if (error) throw error;
      return true;
    } catch (err: any) {
      setError(err.message);
      console.error('–û—à–∏–±–∫–∞ –ø—Ä–∏ —É–¥–∞–ª–µ–Ω–∏–∏ —É—á–µ–Ω–∏–∫–∞:', err);
      return false;
    } finally {
      setLoading(false);
    }
  };

  return {
    loading,
    error,
    fetchStudents,
    createStudent,
    updateStudent,
    deleteStudent,
  };
};