// –†–µ–∞–ª–∏–∑–æ–≤–∞—Ç—å —Ä–µ–¥–∞–∫—Ç–∏—Ä–æ–≤–∞–Ω–∏–µ —É—á–µ–Ω–∏–∫–∞ –∏ —É–¥–∞–ª–µ–Ω–∏–µ, —Å–¥–µ–ª–∞—Ç—å loader
"use client";

import React, { useState, useEffect } from "react";
import { Group, Student, Transaction } from "../types";
import { useLocalStorage } from "../hooks/useLocalStorage";
import { Button } from "../components/ui/Button";
import { GroupCard } from "../components/GroupCard";
import { StudentCard } from "../components/StudentCard";
import { CreateGroupModal } from "../components/CreateGroupModal";
import { AddStudentModal } from "../components/AddStudentModal";
import { StudentDetailsModal } from "../components/StudentDetailsModal";
import { EditGroupModal } from "../components/EditGroupModal";
import { EditStudentModal } from "../components/EditStudentModal";
import { BulkRewardModal } from "../components/BulkRewardModal";
import { toast } from "react-toastify";
import useSound from "use-sound";
import { useGroups } from "@/hooks/useGroups";
import { useStudents } from "@/hooks/useStudents";
import { useTransactions } from "@/hooks/useTransactions";
import { supabase } from "@/lib/supabase";

const NOTIFICATION_SOUND = "/sound/notification.mp3";

export default function Page() {
  const [groups, setGroups] = useState<Group[]>([]);
  const [students, setStudents] = useState<Student[]>([]);

  const [studentTransactions, setStudentTransactions] = useState<Transaction[]>(
    []
  );
  const [allStudents, setAllStudents] = useState<Student[]>([]);
  const [currentGroupId, setCurrentGroupId] = useState<string | null>(null);
  const [selectedStudent, setSelectedStudent] = useState<Student | null>(null);
  const [showCreateGroup, setShowCreateGroup] = useState(false);
  const [showAddStudent, setShowAddStudent] = useState(false);
  const [showStudentDetails, setShowStudentDetails] = useState(false);
  const [showEditGroup, setShowEditGroup] = useState(false);
  const [showEditStudent, setShowEditStudent] = useState(false);
  const [showAddAll, setShowAddAll] = useState(false);
  const [editingGroup, setEditingGroup] = useState<Group | null>(null);
  const [editingStudent, setEditingStudent] = useState<Student | null>(null);
  const { fetchGroups, createGroup, updateGroup, loading, error } = useGroups();
  const {
    fetchStudents,
    createStudent,
    deleteStudent,
    updateStudent,
    loading: studentsLoading,
  } = useStudents();
  const { fetchTransactions, createTransaction, bulkCreateTransactions } =
    useTransactions();

  useEffect(() => {
    const loadAllStudents = async () => {
      // –ü—Ä–µ–¥–ø–æ–ª–∞–≥–∞–µ—Ç—Å—è, —á—Ç–æ fetchStudents() –±–µ–∑ –∞—Ä–≥—É–º–µ–Ω—Ç–æ–≤ –≤–æ–∑–≤—Ä–∞—â–∞–µ—Ç –≤—Å–µ—Ö
      const data = await fetchStudents(); // ‚Üê —É–±–µ–¥–∏—Ç–µ—Å—å, —á—Ç–æ —Ö—É–∫ –ø–æ–¥–¥–µ—Ä–∂–∏–≤–∞–µ—Ç —ç—Ç–æ!
      if (data) {
        setAllStudents(data);
      }
    };
    loadAllStudents();
  }, []);

  useEffect(() => {
    const loadGroupsFromSupabase = async () => {
      const data = await fetchGroups();
      if (data) {
        setGroups(data);
      }
    };
    loadGroupsFromSupabase();
  }, []);

  useEffect(() => {
    if (currentGroupId) {
      fetchStudents(currentGroupId).then((data) => {
        if (data) {
          setStudents(data);
        } else {
          setStudents([]);
        }
      });
    } else {
      setStudents([]); // –æ—á–∏—â–∞–µ–º –ø—Ä–∏ –≤—ã—Ö–æ–¥–µ –∏–∑ –≥—Ä—É–ø–ø—ã
    }
  }, [currentGroupId]);

  useEffect(() => {
    if (selectedStudent) {
      fetchTransactions(selectedStudent.id).then((data) => {
        if (data) setStudentTransactions(data);
      });
    }
  }, [selectedStudent]);

  const currentGroup = groups.find((g) => g.id === currentGroupId);
  const currentGroupStudents = students.filter(
    (s) => s.groupId === currentGroupId
  );

  // –ó–≤—É–∫ –¥–ª—è —É–≤–µ–¥–æ–º–ª–µ–Ω–∏—è
  const [playNotification] = useSound(NOTIFICATION_SOUND, {
    volume: 0.4,
  });

  const handleCreateGroup = async (name: string) => {
    const newGroup = await createGroup(name);
    if (newGroup) {
      setGroups((prev) => [...prev, newGroup]);
      toast.success("–ì—Ä—É–ø–ø–∞ —Å–æ–∑–¥–∞–Ω–∞!");
      playNotification();
      setShowCreateGroup(false);
    } else {
      console.log("–û—à–∏–±–∫–∞");
    }
  };

  const handleAddStudent = async (name: string, birthDate: string) => {
    if (!currentGroupId) return;

    const newStudent = await createStudent(name, birthDate, currentGroupId);
    if (newStudent) {
      setStudents((prev) => [...prev, newStudent]);
      toast.success("–£—á–µ–Ω–∏–∫ –¥–æ–±–∞–≤–ª–µ–Ω!");
      playNotification();
      setShowAddStudent(false);
    }
  };

  const addTransaction = async (
    studentId: string,
    amount: number,
    description: string
  ) => {
    // 1. –°–Ω–∞—á–∞–ª–∞ —Å–æ–∑–¥–∞—ë–º —Ç—Ä–∞–Ω–∑–∞–∫—Ü–∏—é
    const { data: newTransaction, error: txError } = await supabase
      .from("Transactions")
      .insert({
        student_id: studentId,
        amount,
        description,
      })
      .select()
      .single();

    if (txError) {
      console.error("–û—à–∏–±–∫–∞ —Å–æ–∑–¥–∞–Ω–∏—è —Ç—Ä–∞–Ω–∑–∞–∫—Ü–∏–∏:", txError);
      toast.error("–ù–µ —É–¥–∞–ª–æ—Å—å —Å–æ–∑–¥–∞—Ç—å —Ç—Ä–∞–Ω–∑–∞–∫—Ü–∏—é");
      return;
    }

    // 2. –ó–∞—Ç–µ–º –æ–±–Ω–æ–≤–ª—è–µ–º –±–∞–ª–∞–Ω—Å —É—á–µ–Ω–∏–∫–∞
    const { error: balError } = await supabase
      .from("Students")
      .update({ balance: selectedStudent!.balance + amount })
      .eq("id", studentId);

    if (balError) {
      console.error("–û—à–∏–±–∫–∞ –æ–±–Ω–æ–≤–ª–µ–Ω–∏—è –±–∞–ª–∞–Ω—Å–∞:", balError);
      toast.error("–¢—Ä–∞–Ω–∑–∞–∫—Ü–∏—è —Å–æ–∑–¥–∞–Ω–∞, –Ω–æ –±–∞–ª–∞–Ω—Å –Ω–µ –æ–±–Ω–æ–≤–ª—ë–Ω");
      return;
    }

    // 3. –û–±–Ω–æ–≤–ª—è–µ–º –ª–æ–∫–∞–ª—å–Ω—ã–π —Å—Ç–µ–π—Ç
    setStudents((prev) =>
      prev.map((s) =>
        s.id === studentId ? { ...s, balance: s.balance + amount } : s
      )
    );

    if (selectedStudent?.id === studentId) {
      setStudentTransactions((prev) => [
        {
          id: newTransaction.id,
          studentId: newTransaction.student_id,
          amount: newTransaction.amount,
          type: newTransaction.amount >= 0 ? "add" : "subtract",
          description: newTransaction.description,
          timestamp: newTransaction.created_at
            ? new Date(newTransaction.created_at)
            : new Date(),
        },
        ...prev,
      ]);
    }

    toast.success("–¢—Ä–∞–Ω–∑–∞–∫—Ü–∏—è –¥–æ–±–∞–≤–ª–µ–Ω–∞");
  };

  const handleDeleteStudent = async (studentId: string) => {
    const success = await deleteStudent(studentId);
    if (success) {
      // –£–¥–∞–ª—è–µ–º –∏–∑ –ª–æ–∫–∞–ª—å–Ω–æ–≥–æ —Å—Ç–µ–π—Ç–∞
      setStudents((prev) => prev.filter((s) => s.id !== studentId));

      // –ï—Å–ª–∏ –æ—Ç–∫—Ä—ã—Ç –¥–µ—Ç–∞–ª—å–Ω—ã–π –ø—Ä–æ—Å–º–æ—Ç—Ä ‚Äî –∑–∞–∫—Ä—ã–≤–∞–µ–º
      if (selectedStudent?.id === studentId) {
        setShowStudentDetails(false);
        setSelectedStudent(null);
      }

      toast.success("–£—á–µ–Ω–∏–∫ —É–¥–∞–ª—ë–Ω");
    } else {
      toast.error("–ù–µ —É–¥–∞–ª–æ—Å—å —É–¥–∞–ª–∏—Ç—å —É—á–µ–Ω–∏–∫–∞");
    }
  };

  const handleUpdateGroup = async (groupId: string, name: string) => {
    const success = await updateGroup(groupId, name);
    if (success) {
      setGroups((prev) =>
        prev.map((g) => (g.id === groupId ? { ...g, name } : g))
      );
      toast.success("–ì—Ä—É–ø–ø–∞ –æ–±–Ω–æ–≤–ª–µ–Ω–∞!");
      setShowEditGroup(false);
    }
  };

  const handleUpdateStudent = async (
    studentId: string,
    name: string,
    birthDate: string
  ) => {
    const success = await updateStudent(studentId, name, birthDate);

    if (success) {
      setStudents((prev) =>
        prev.map((s) => (s.id === studentId ? { ...s, name, birthDate } : s))
      );

      toast.success("–£—á–µ–Ω–∏–∫ –æ–±–Ω–æ–≤–ª–µ–Ω");
      setShowEditStudent(false);
      if (selectedStudent?.id === studentId) {
        setSelectedStudent((prev) =>
          prev ? { ...prev, name, birthDate } : null
        );
      }
    }
  };

  // Helper function to format date for input type="date"
  const formatDateForInput = (dateString: string) => {
    try {
      const date = new Date(dateString);
      return date.toISOString().split("T")[0];
    } catch {
      return dateString;
    }
  };

  const handleApplyToAll = async (amount: number, description: string) => {
    if (!currentGroupId) return;

    const studentsInGroup = students.filter(
      (s) => s.groupId === currentGroupId
    );

    // 1. –°–æ–∑–¥–∞—ë–º –≤—Å–µ —Ç—Ä–∞–Ω–∑–∞–∫—Ü–∏–∏
    const transactionsData = studentsInGroup.map((student) => ({
      student_id: student.id,
      amount,
      description,
    }));

    const { error: txError } = await supabase
      .from("Transactions")
      .insert(transactionsData);

    if (txError) {
      console.error("–û—à–∏–±–∫–∞ –º–∞—Å—Å–æ–≤–æ–≥–æ —Å–æ–∑–¥–∞–Ω–∏—è —Ç—Ä–∞–Ω–∑–∞–∫—Ü–∏–π:", txError);
      toast.error("–ù–µ —É–¥–∞–ª–æ—Å—å —Å–æ–∑–¥–∞—Ç—å —Ç—Ä–∞–Ω–∑–∞–∫—Ü–∏–∏");
      return;
    }

    // 2. –û–±–Ω–æ–≤–ª—è–µ–º –±–∞–ª–∞–Ω—Å –∫–∞–∂–¥–æ–≥–æ —É—á–µ–Ω–∏–∫–∞
    const updates = studentsInGroup.map((student) =>
      supabase
        .from("Students")
        .update({ balance: student.balance + amount })
        .eq("id", student.id)
    );

    const results = await Promise.all(updates);
    const hasError = results.some((r) => r.error);
    if (hasError) {
      console.error(
        "–û—à–∏–±–∫–∏ –ø—Ä–∏ –æ–±–Ω–æ–≤–ª–µ–Ω–∏–∏ –±–∞–ª–∞–Ω—Å–æ–≤:",
        results.filter((r) => r.error)
      );
      toast.error("–ß–∞—Å—Ç–∏—á–Ω–æ –Ω–µ —É–¥–∞–ª–æ—Å—å –æ–±–Ω–æ–≤–∏—Ç—å –±–∞–ª–∞–Ω—Å—ã");
    }

    // 3. –û–±–Ω–æ–≤–ª—è–µ–º –ª–æ–∫–∞–ª—å–Ω—ã–π —Å—Ç–µ–π—Ç
    setStudents((prev) =>
      prev.map((s) =>
        studentsInGroup.some((st) => st.id === s.id)
          ? { ...s, balance: s.balance + amount }
          : s
      )
    );

    toast.success(`–ù–∞—á–∏—Å–ª–µ–Ω–æ ${amount}–ö –≤—Å–µ–º —É—á–µ–Ω–∏–∫–∞–º`);
    playNotification();
    setShowAddAll(false);
  };

  if (currentGroupId && currentGroup) {
    return (
      <div className="min-h-screen bg-gray-900 p-4" data-oid="8k330ln">
        <div className="max-w-4xl mx-auto" data-oid="5731kn-">
          <div
            className="flex flex-col gap-7 mb-10"
            data-oid="n0.33ma"
            key="olk-t3Sk"
          >
            <h1
              className="text-2xl font-bold text-white text-center"
              data-oid="nfh55sd"
            >
              {currentGroup.name}
            </h1>
            <div className="flex justify-between">
              <Button
                onClick={() => setCurrentGroupId(null)}
                variant="secondary"
                data-oid="moh7jhq"
                className="w-max h-max md:row-start-2 md:col-start-1 lg:w-max lg:self-center"
              >
                ‚Üê&nbsp;–ù–∞–∑–∞–¥
              </Button>

              <div
                className="flex flex-wrap gap-2 justify-end
               md:row-start-2 md:col-start-4 md:col-span-3
               lg:justify-end lg:self-center"
              >
                <Button
                  onClick={() => setShowAddStudent(true)}
                  data-oid="pn7et30"
                >
                  + –£—á–µ–Ω–∏–∫
                </Button>
                <Button
                  onClick={() => setShowAddAll(true)}
                  disabled={students.length < 1}
                >
                  –ù–∞—á–∏—Å–ª–∏—Ç—å –≤—Å–µ–º
                </Button>
              </div>
            </div>
          </div>
          <div
            className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4"
            data-oid="yqf3y._"
            key="olk-CXuA"
          >
            {currentGroupStudents.map((student) => (
              <StudentCard
                key={student.id}
                student={student}
                onClick={() => {
                  setSelectedStudent(student);
                  setShowStudentDetails(true);
                }}
                data-oid="g8jew3n"
              />
            ))}
          </div>
        </div>

        <AddStudentModal
          isOpen={showAddStudent}
          onClose={() => setShowAddStudent(false)}
          onAdd={handleAddStudent}
          data-oid="64c_cci"
        />

        <StudentDetailsModal
          isOpen={showStudentDetails}
          onClose={() => setShowStudentDetails(false)}
          student={selectedStudent}
          transactions={studentTransactions}
          onAddTransaction={(amount, description) => {
            if (selectedStudent) {
              addTransaction(selectedStudent.id, amount, description);
            }
          }}
          onDeleteStudent={() => {
            if (selectedStudent) {
              handleDeleteStudent(selectedStudent.id);
            }
          }}
          onEditStudent={() => {
            if (selectedStudent) {
              setEditingStudent(selectedStudent);
              setShowEditStudent(true);
              setShowStudentDetails(false);
            }
          }}
          data-oid="iuj2wa1"
        />

        <EditStudentModal
          isOpen={showEditStudent}
          onClose={() => setShowEditStudent(false)}
          onUpdate={(name, birthDate) => {
            if (editingStudent) {
              handleUpdateStudent(editingStudent.id, name, birthDate);
            }
          }}
          currentName={editingStudent?.name || ""}
          currentBirthDate={
            editingStudent?.birthDate
              ? formatDateForInput(editingStudent.birthDate)
              : ""
          }
          data-oid="_ujuvvz"
        />

        <BulkRewardModal
          isOpen={showAddAll}
          onClose={() => setShowAddAll(false)}
          onApplyToAll={handleApplyToAll}
          data-oid="bulk-reward-modal"
        />
      </div>
    );
  }

  return (
    <div className="min-h-screen bg-gray-900 p-4" data-oid="2x50ljn">
      <div className="max-w-4xl mx-auto" data-oid="97z8c7:">
        <div className="text-center mb-8" data-oid="k8e4t8h">
          <h1 className="text-3xl font-bold text-white mb-2" data-oid="w9bw_u:">
            –£—á–µ—Ç –∫–∏–±–µ—Ä–æ–Ω–æ–≤
          </h1>
          <p className="text-gray-400" data-oid="p_:x5:0">
            –£–ø—Ä–∞–≤–ª—è–π—Ç–µ –∫–∏–±–µ—Ä–æ–Ω–∞–º–∏ –≥—Ä—É–ø–ø—ã
          </p>
        </div>

        <div className="flex justify-center mb-8" data-oid="sadluj5">
          <Button
            onClick={() => setShowCreateGroup(true)}
            size="lg"
            data-oid="lekm_k-"
          >
            + –°–æ–∑–¥–∞—Ç—å –≥—Ä—É–ø–ø—É
          </Button>
        </div>

        <div
          className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4"
          data-oid="6-qrw-6"
        >
          {loading ? (
            <div className="col-span-full text-center py-4">–ó–∞–≥—Ä—É–∑–∫–∞...</div>
          ) : (
            groups.map((group) => {
              console.log("Students: ", allStudents);
              const studentsCount = allStudents.filter(
                (s) => s.groupId == group.id
              ).length;
              return (
                <GroupCard
                  key={group.id}
                  group={group}
                  studentsCount={studentsCount}
                  onClick={() => setCurrentGroupId(group.id)}
                  onEdit={(e) => {
                    e.stopPropagation();
                    setEditingGroup(group);
                    setShowEditGroup(true);
                  }}
                  data-oid="angyy_:"
                />
              );
            })
          )}
        </div>
      </div>

      <CreateGroupModal
        isOpen={showCreateGroup}
        onClose={() => setShowCreateGroup(false)}
        onCreate={handleCreateGroup}
        data-oid="yf0srr2"
      />

      <EditGroupModal
        isOpen={showEditGroup}
        onClose={() => setShowEditGroup(false)}
        onUpdate={(name) => {
          if (editingGroup) {
            handleUpdateGroup(editingGroup.id, name);
          }
        }}
        currentName={editingGroup?.name || ""}
        data-oid="u7u1363"
      />
    </div>
  );
}
